@charset "UTF-8";
/**
* 編集画面用CSS
*/
:root {
  --color-text: #2C2C2C;
  --color-accent: #6F7AFF;
  --color-background: #F5F5F5;
  --color-link: #2E37A5;
  --color-hover: #4D68FF;
  --color-shadow: #2C2C2C;
  --color-white: #FFFFFF;
  --color-red: #BF008C;
  --color-yellow: #EAC159;
  --color-orange: #FF4D4D;
  --color-pink: #FFEFEF;
  --color-green: #009111;
  --color-lightgreen: #EFFFF7;
  --color-blue: #2E37A5;
  --color-lightblue: #DBE2FF;
  --color-gray: #BFBFBF;
  --color-lightgray: #F5F5F5;
  --color-darkgray: #444558;
  --color-beige: #FFF0DD;
  --color-brown: #834700;
  --color-gradient-title: transparent linear-gradient(268deg, #2C2CAA 0%, #41323E 100%) 0% 0% no-repeat padding-box;
  --color-gradient-bg: transparent linear-gradient(180deg, #F0F4FF 0%, #FFFFFF 100%) 0% 0% no-repeat padding-box;
  --color-gradient-heading: transparent linear-gradient(95deg, #FFEDF4 0%, #F3DDFF 51%, #D9F8FF 100%) 0 0 no-repeat padding-box;
  --color-gradient-button: transparent linear-gradient(98deg, #7E7EFF 0%, #4A97FF 100%) 0 0 no-repeat padding-box;
  --color-gradient-button-hover: transparent linear-gradient(98deg, #FFFFFF 0%, #A8CDFF 100%) 0 0 no-repeat padding-box;
  --font-base: "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro","游ゴシック Medium", "Yu Gothic Medium", "游ゴシック体", YuGothic, "メイリオ";
  --font-heading: "Hiragino Mincho Pro", "ヒラギノ明朝 Pro";
  --font-weight-bold: 500;
}

* {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  padding: 0;
  margin: 0;
  border: 0;
}

a {
  outline: none;
  -webkit-touch-callout: none;
}

a:focus,
*:focus {
  outline: none;
}

a {
  color: inherit;
}

article,
aside,
footer,
header,
nav,
section,
main {
  display: block;
}

img,
video {
  max-width: 100%;
  height: auto;
}

img {
  vertical-align: top;
  border-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
  content: none;
}

[hidden] {
  display: none !important;
}

[disabled] {
  cursor: not-allowed;
}

:focus:not(:focus-visible) {
  outline: none;
}

/* form */
button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input[type=text],
input[type=tel],
input[type=number],
input[type=date],
input[type=time],
input[type=password],
input[type=email],
textarea {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

textarea {
  display: block;
  overflow: auto;
}

fieldset {
  margin: 0;
  padding: 0.35em 0.75em 0.625em;
  border: none;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body {
  color: var(--color-text);
  font-family: var(--font-base), sans-serif;
  font-optical-sizing: auto;
  font-feature-settings: "palt";
  font-size: 16px;
  line-height: 1.625;
}

p {
  margin-bottom: 1em;
  line-height: 1.875;
}

a {
  color: var(--color-text);
  transition: color 0.5s, text-decoration 0.5s;
}
a:hover {
  color: var(--color-hover);
}
a:hover img {
  opacity: 0.65;
}

b,
strong {
  font-weight: bolder;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

h2 {
  margin-top: 1.5em;
  margin-bottom: 1em;
}
@media not all and (min-width: 782px) {
  h2 {
    font-size: clamp(20px, 5.2vw, 24px);
  }
}
@media (min-width: 782px) {
  h2 {
    font-size: 32px;
  }
}

h3 {
  margin-top: 1em;
  margin-bottom: 1em;
}
@media not all and (min-width: 782px) {
  h3 {
    font-size: 18px;
  }
}
@media (min-width: 782px) {
  h3 {
    font-size: 20px;
  }
}

h4 {
  margin-top: 1em;
  margin-bottom: 0.5em;
  font-size: 1.1rem;
}

h5,
h6 {
  font-size: 1rem;
  margin-top: 1.5em;
  margin-bottom: 0.5em;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.content ul,
.content ol {
  margin-left: 1.5em;
  margin-bottom: 1.5em;
}
.content ul {
  list-style: disc;
}
.content ul > li::marker {
  color: var(--color-font);
  font-size: 1.25em;
}
.content dl {
  width: 100%;
  margin-bottom: 1.5em;
}

table {
  width: 100%;
  margin-bottom: 4.5em;
}
table th,
table td {
  text-align: left;
  border: 1px solid var(--color-gray);
  padding: 1em;
}
table th {
  background-color: var(--color-lightgray);
  word-break: keep-all;
}
table td {
  background-color: var(--color-white);
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

/**
* Prevent `sub` and `sup` elements from affecting the line height in
* all browsers.
*/
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

@media not all and (min-width: 782px) {
  :root {
    --wp--preset--font-size--large: 24px;
    --wp--preset--font-size--xlarge: 28px;
    --wp--preset--font-size--huge: 32px;
  }
}
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body.menu-open {
  height: 100%;
  overflow: hidden;
}

